.error-message {
    border: 2px solid rgb(239, 110, 110);
    border-radius: 8px;
    padding: 4px;
    background-color: rgb(217, 217, 217);
    color: rgb(232, 59, 59);
    margin: 4px;

}

body {
    background-color: rgb(112, 112, 112);
}

.app-container {
    display:flex;
    flex-direction: column;
    align-items: center;
}

.add-container {
 display:flex;
 flex-direction: column;
 justify-content: space-between;
}

.add-form {
    display:flex;
    justify-content: space-between;
}

.add-btn {
    width:100%;
    background-color: rgb(201, 255, 239);
    border:none;

}

.numbers {
    display:flex;
    flex-direction: column;

}

.title {
    color: white,

}

.contact {
    display: flex;
    justify-content: space-between;
    padding:10px;
}

.name, .number, .delete {
    padding: 2px 20px 0px 10px
}

.delete {
   background-color: rgb(255, 139, 139);
   border: none;
   border-radius: 8px;
   align-items: center;
   
}

.number {
    color: rgb(255, 255, 255)
}

.name {
    color: rgb(45, 192, 255)
}